<template>
    <div>
        <Title text=""></Title>
        <el-alert title="Tips" type="warning" description="" show-icon
                  :closable="false">
        </el-alert>
        <!--  -->
        <el-tree class="am-margin-top-lg" ref="tree" :default-expanded-keys="[1]" lazy node-key="id" :load="loadNode"
                 :props="defaultProps">
            <div class="node-box" slot-scope="{ node, data }">
                <div class="node-name">
                    {{ node.label }}
                </div>
                <div class="node-action">
                    <div class="node-order">
                        <el-tag size="mini">{{ data.order }}</el-tag>
                    </div>
                    <el-button type="text" size="mini" :disabled="data.id==1"
                               @click.stop="openEditModal(node,data)">
                    </el-button>
                    <el-button type="text" size="mini"
                               @click.stop="openAddModal(node,data)">
                    </el-button>
                    <el-button type="text" size="mini" :disabled="data.id==1"
                               @click.stop="openDeleteModal(node,data)">
                    </el-button>
                </div>
            </div>
        </el-tree>
        <!-- Modal -->
        <el-dialog title="" :visible.sync="EditModalVisible">
            <el-form label-width="80px" :label-position="'left'">
                <el-form-item label="">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.path" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="editForm.order" placeholder="2001"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="EditModalVisible = false"> </el-button>
                <el-button type="primary" @click="UpdateNodeHandle"> </el-button>
            </div>
        </el-dialog>
        <!-- Modal -->
        <el-dialog title="" :visible.sync="AddModalVisible">
            <el-form label-width="80px" :label-position="'left'">
                <el-form-item label="">
                    <el-input v-model="addForm.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.path" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="addForm.order" placeholder="2001"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="AddModalVisible = false"> </el-button>
                <el-button type="primary" @click="AddNodeHandle"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {Menu} from "@/api/index";
    import Title from '@/components/Title.vue';

    export default {
        name: "Menu",
        components: {
            Title
        },
        data() {
            return {
                defaultProps: {
                    label: 'name'
                },
                EditModalVisible: false,
                editForm: {
                    name: "",
                    pId: '',
                    path: '',
                    order: '',
                },
                AddModalVisible: false,
                addForm: {
                    name: "",
                    pId: '',
                    path: '',
                    order: '',
                },
                currentNodeData: '',
                currentNode: '',
            }
        },
        methods: {
            async loadNode(node, resolve) {
                // level==0
                if (node.level === 0) {
                    let {data} = await Menu.load({pId: 0});
                    return resolve(data);
                }
                let {data} = await Menu.load({pId: node.data.id});
                resolve(data);
            },
            // Modal
            openEditModal(node, data) {
                this.EditModalVisible = true;
                this.editForm = {...data};
                // node
                this.currentNode = node;
            },
            // ModalpId
            openAddModal(node, data) {
                this.addForm.pId = data.id;
                this.AddModalVisible = true;
                // data
                this.currentNodeData = data;
            },
            // 
            openDeleteModal(node, data) {
                this.$msgbox({
                    type: 'warning',
                    title: "",
                    message: ', ?'
                }).then(async () => {
                    let {status} = await Menu.remove({id: data.id});
                    if (status) {
                        node.remove();
                        this.$message.success('!');
                    }
                })
            },
            // 
            async AddNodeHandle() {
                let {status, msg, data} = await Menu.add({...this.addForm});
                if (!status) {
                    this.$message.error(msg);
                    return false;
                }
                this.$message.success(msg);
                this.AddModalVisible = false;
                // 
                let nodeData = this.currentNodeData;
                let newChild = {...this.addForm, ...data};
                if (!nodeData.children) {
                    this.$set(nodeData, 'children', []);
                }
                nodeData.children.push(newChild);
                //
                this.addForm = {name: "", pId: '', path: ''};
            },
            // 
            async UpdateNodeHandle() {
                let {status, msg} = await Menu.update({...this.editForm});
                if (status) {
                    this.$message.success(msg);
                    this.EditModalVisible = false;
                    // 
                    this.currentNode.data = {...this.editForm}
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    .node-box {
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;

        .node-order {
            width: 180px;
            display: inline-block;
            font-size: 12px;
        }
    }
</style>