<template>
    <div>
        <Title text=""></Title>
        <el-row :gutter="10">
            <el-col :span="10">
                <el-card class="role-card">
                    <div slot="header" class="header">
                        <span></span>
                        <el-button plain type="primary" @click="AddModalVisible = true"
                                   icon="el-icon-circle-plus-outline">
                        </el-button>
                    </div>
                    <el-table :data="tableData" style="width: 100%">
                        <el-table-column
                                prop="id"
                                label="#">
                        </el-table-column>
                        <el-table-column
                                label="">
                            <template slot-scope="scope">
                                <el-tag :type="scope.row.id===1?'danger':''">{{scope.row.name}}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column width="180"
                                         label="">
                            <template slot-scope="scope">
                                <el-button :disabled="scope.row.id===1" type="primary" plain
                                           @click="openEditModal(scope.row)"
                                           icon="el-icon-edit"></el-button>
                                <el-button :disabled="scope.row.id===1" type="danger" plain
                                           @click="openDeleteModal(scope.row.id)"
                                           icon="el-icon-delete"></el-button>
                                <router-link class="am-margin-left-sm"
                                             :to="{ name: 'RoleConfig', params: { id: scope.row.id }}">
                                    <el-button type="primary" plain icon="el-icon-setting"></el-button>
                                </router-link>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-card>
            </el-col>
            <el-col :span="14">
                <router-view></router-view>
            </el-col>
        </el-row>
        <!-- Modal -->
        <el-dialog title="" :visible.sync="AddModalVisible">
            <el-form label-width="80px" :label-position="'left'">
                <el-form-item label="">
                    <el-input v-model="addForm.name"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="AddModalVisible = false"> </el-button>
                <el-button type="primary" @click="addRoleHandle"> </el-button>
            </div>
        </el-dialog>
        <!-- Modal -->
        <el-dialog title="" :visible.sync="EditModalVisible">
            <el-form label-width="80px" :label-position="'left'">
                <el-form-item label="">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="EditModalVisible = false"> </el-button>
                <el-button type="primary" @click="updateRoleHandle"> </el-button>
            </div>
        </el-dialog>
    </div>

</template>

<script>
    import {Role} from "@/api/index";
    import Title from '@/components/Title.vue';

    export default {
        name: "Role",
        components: {
            Title
        },
        data() {
            return {
                tableData: [],
                AddModalVisible: false,
                addForm: {
                    name: "",
                },
                EditModalVisible: false,
                editForm: {
                    id: "",
                    name: "",
                },
            }
        },
        created() {
            this.loadRole();
        },
        methods: {
            async loadRole() {
                let {status, data} = await Role.load();
                if (status) {
                    this.tableData = data;
                }
            },
            //
            async addRoleHandle() {
                let {status} = await Role.add({...this.addForm});
                if (status) {
                    this.loadRole();
                    this.AddModalVisible = false;
                }
            },
            //Modal
            openEditModal(data) {
                this.EditModalVisible = true;
                this.editForm = {...data};
            },
            //
            async updateRoleHandle() {
                let {status} = await Role.update({...this.editForm});
                if (status) {
                    this.loadRole();
                    this.EditModalVisible = false;
                }
            },
            // 
            openDeleteModal(id) {
                this.$msgbox({
                    type: 'warning',
                    title: "",
                    message: ', ',
                    showCancelButton: true,
                }).then(async () => {
                    let {status} = await Role.remove({id});
                    if (status) {
                        this.loadRole();
                        this.$message.success('!');
                    }
                })
            },
        }
    }
</script>

<style lang="scss" scoped>
    .role-card {
        .header {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
</style>